<template>
    <div class="leader">
        <header>
            <van-icon name="hot" />
            <van-search v-model="value" placeholder="请输入搜索关键词" @click="$router.push('/home/search')"/>
            <van-icon name="chat" />
        </header>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <!-- 轮播图 -->
            <van-swipe class="my-swipe" :autoplay="1500" indicator-color="white">
                <van-swipe-item v-for="(item,index) in swiperList" :key="index" class="top-swiper">
                    <img :src="item.img" alt="">
                </van-swipe-item>
            </van-swipe>
            <!-- 静态滑动轮播导航 -->
            <van-swipe class="my-swiper" indicator-color="white">
                <van-swipe-item class="center-swiper">
                    <ul class="ul">
                        <li v-for="(item,index) in centerListleft" :key="index">
                            <img :src="item.img" alt="">
                            <p>{{ item.title }}</p>
                        </li>
                    </ul>
                </van-swipe-item>
                <van-swipe-item class="center-swiper">
                    <ul class="ul">
                        <li v-for="(item,index) in centerListright" :key="index">
                            <img :src="item.img" alt="">
                            <p>{{ item.title }}</p>
                        </li>
                    </ul>
                </van-swipe-item>
            </van-swipe>
            <!-- 商品列表数据 -->
            <div class="shoplist">
                <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
                >
                    <van-card
                    v-for="(item,index) in shopList" :key="index"                        
                    v-if="index<aa"
                    :price="item.price"
                    :title="item.title"
                    :thumb="item.pic"
                    >
                    <template #footer>
                        <van-button size="mini" type="danger" @click="ajaxtan(item)"><van-icon name="cart" /></van-button>
                    </template>
                    </van-card>
                </van-list>
            </div>
            <!-- 自定义图标 -->
            <van-popup
            v-model="show"
            closeable
            close-icon="close"
            position="bottom"
            :style="{ height: '50%' }"
            >
                <ol>
                    <li v-for="(item,index) in shopCart" :key="index">
                        <img :src="item.pic" alt="">
                        <p class="list-p1">{{ item.title }}</p>
                        <p class="list-p2">
                            {{ item.price }}
                        </p>
                        <van-button type="info" @click="addshoplist(item)">加入购物车</van-button>
                    </li>
                </ol>
            </van-popup>
        </van-pull-refresh>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            value:'',
            count: 0,
            isLoading: false,
            // 轮播图数组
            swiperList:[],
            // 中间轮播图的数据
            centerListleft:[],
            centerListright:[],
            // 商品列表得数据
            shopList:JSON.parse(localStorage.getItem('token'))||[],
            // 弹出层
            show:false,
            shopCart:JSON.parse(localStorage.getItem('token'))||[],
            loading: false,
            finished: false,
            aa:4
        };
    },
    created() {

    },
    mounted() {
        // 获取轮播图数据
        this.ajaxSwiper()
        // 中间静态轮播数据
        this.ajaxCenter()
        this.ajaxCenterImg()
        // 获取列表的数据
        this.ajaxshoplist()
    },
    methods: {
        // 上拉刷新
        onRefresh() {
            setTimeout(() => {
                Toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 1000);
         },
        //  获取轮播图数据
        async ajaxSwiper(){
            let {data:swiper} = await this.$axios.get('/data.json')
            this.swiperList = swiper
        },
        // 中间轮播图的数据
        async ajaxCenter(){
            let {data:center} = await this.$axios.get('/swiper.json')
            this.centerListleft = center.result
            // console.log(this.centerListleft)
        },
         // 中间轮播图的数据
        async ajaxCenterImg(){
            let {data:center} = await this.$axios.get('/swiper.json')
            this.centerListright = center.cenImg
            // console.log(this.centerListright)
        },
        // 商品列表的数据
        async ajaxshoplist(){
            let {data:shop} = await this.$axios.get('/list.json')
            this.shopList = shop.result
            // console.log(this.shopList)
        },
        // 弹出层
        ajaxtan(item){
            this.shopCart = []
            this.show = !this.show
            this.shopCart.push(item)
        },
        // 添加商品到购物车页面
        addshoplist(i){
            let index = this.$store.state.cart.findIndex((item)=>{
                return i._id == item._id
            })
            let obj = {
                _id:i._id,
                title:i.title,
                pic:i.pic,
                price:i.price,
                bool:i.bool,
                number:i.number
            }
            if(index==-1){
                this.$store.commit('add',obj)
            }else{
                this.$store.commit('addIndex',index)
            }
            this.$router.push('/home/cart')
        },
        onLoad() {
        // 异步更新数据
        // setTimeout 仅做示例，真实场景中一般为 ajax 请求
        setTimeout(() => {
            
            this.aa+=2
            // 加载状态结束
            this.loading = false
            // 数据全部加载完成
            if (this.aa==this.shopList.length ) {
                this.finished = true;
            }
        }, 1500);
        },
    }
};
</script>

<style scoped lang="scss">
    .van-list{
        margin-bottom: 50px;
    }
    .leader{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    header{
        width: 100%;
        display: flex;
        align-items: center;
        height: 54px;
        justify-content: space-around;
        background: #A40000;
    }
    header .van-search{
        width: 80%;
        background: #A40000;
    }
    .van-icon{
        font-size: 25px;
        color: #fff;
    }
    .van-pull-refresh{
        flex: 1;
        overflow: auto;
    }
    .van-pull-refresh::-webkit-scrollbar { display: none }
    /* 轮播图 */
    .my-swipe .van-swipe-item {
        font-size: 20px;
        height: 150px;
        text-align: center;
        margin-top: 10px;
    }
    .top-swiper img{
        width: 100%;
        height: 150px;
    }
    /* 中间的轮播图 */
    .my-swiper .van-swipe-item {
        font-size: 20px;
        height: 240px;
        text-align: center;
        margin-top: 10px;
    }
    .my-swiper{
        display: flex;
        align-items: center;
    }
    .center-swiper img{
        width: 70px;
        height: 70px;
    }
    // 中间导航静态轮播
    .ul{
        width: 100%;
        height: 240px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        li{
            width: 25%;
            p{
                font-size: 14px;
            }
        }
    }
    // 列表的数据
    .shoplist{
        width: 100%;
        ol{
            width: 100%;
            flex-wrap: wrap;
            display: flex;
            justify-content: space-around;
            li{
                width: 49%;
                margin-top: 5px;
                img{
                    width: 200px;
                    height: 180px;
                }
                .van-icon{
                    color: darkgray;
                }
            }   
        }    
    }
        ol{
            width: 100%;
            flex-wrap: wrap;
            display: flex;
            justify-content: space-around;
            li{
                width: 49%;
                margin-top: 5px;
                img{
                    width: 200px;
                    height: 180px;
                }
                .list-p1{
                    width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: 14px;
                    margin-top: 10px;
                }
                .list-p2{
                    font-size: 17px;
                    color: #A40000;
                    margin-top: 15px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    .van-icon{
                        color: darkgray;
                        margin-left: 130px;
                    }
                }
            }   
        }
</style>
